@import '~assets/styles/h3Imports';

:root {
  --custom-gray-5: var(--gray-5);
  --separator-color: #dedee6;
  --min-width-dropdown: 0;
  --max-width-dropdown: 100%;
  --accordion-padding: #{rem(16)};
}

.accordion-past {
  --custom-gray-5: var(--gray-3);
}

.accordion {
  figure[data-size='small'] {
    height: rem(48);
    width: rem(48);
  }

  div [data-variant='info'] {
    max-width: rem(668);
  }

  dt {
    display: block;
    min-height: 0;
    padding: 0;
    position: relative;

    [data-accordion-button-element] {
      position: absolute;
      right: rem(8);
      top: 50%;
      transform: translateY(-50%);
    }
  }

  [data-content-collapsed='true'] {
    overflow: hidden;
  }

  [data-content-collapsed='false'] {
    animation: 1s delay-overflow;
    overflow: visible;
  }

  dd {
    padding: 0;
  }

  [data-accordion-element='container']:not(:first-child) {
    margin-top: rem(16);
  }
}

@keyframes delay-overflow {
  from {
    overflow: hidden;
  }
}

.row {
  --gutter-x: #{rem(16)};
  --gutter-y: 0;

  display: flex;
  flex-wrap: wrap;
  margin-left: calc(-0.5 * var(--gutter-x));
  margin-right: calc(-0.5 * var(--gutter-x));
  margin-top: calc(-1 * var(--gutter-y));
}

.col {
  flex-shrink: 0;
  margin-top: var(--gutter-y);
  max-width: 100%;
  padding-left: calc(var(--gutter-x) * 0.5);
  padding-right: calc(var(--gutter-x) * 0.5);
  position: relative;
  width: 100%;
}

.col-first {
  flex: 0 0 auto;
  width: 21.5%;
}

.col-second {
  flex: 0 0 auto;
  width: 23%;
}

.col-third {
  flex: 1 0 0%;
  padding-right: rem(120);
}

.header {
  min-height: rem(84);
  padding: var(--accordion-padding);
}

.header-content {
  align-items: center;
}

.header-title-mobile {
  display: none;
}

.time {
  position: relative;
}

.time-start {
  color: var(--custom-gray-5);
  font-size: rem(27);
  font-weight: 700;
  line-height: 1.185;
  margin: 0;
}

.time-duration {
  color: var(--gray-3);
  font-size: rem(14);
  font-weight: 600;
  line-height: 1.43;
}

.user {
  align-items: center;
  display: flex;
}

.user-avatar {
  align-items: center;
  background-color: var(--background-1);
  border: 1px solid var(--gray-1);
  border-radius: 50%;
  color: var(--gray-3);
  display: flex;
  flex-shrink: 0;
  font-size: rem(19);
  font-weight: 700;
  height: rem(48);
  justify-content: center;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  vertical-align: top;
  width: rem(48);
}

.user-avatar-img {
  border-radius: 50%;
  height: 100%;
  object-fit: cover;
  width: 100%;
}

.user-text {
  color: var(--custom-gray-5);
  font-size: rem(16);
  font-weight: 700;
  line-height: 1.25;
  margin: 0;
  padding-left: var(--gutter-x);

  span {
    color: var(--gray-3);
    font-size: rem(14);
    font-weight: 600;
    line-height: 1.43;
  }
}

.location {
  padding-left: rem(32);
  position: relative;

  &::before {
    background-image: url('../../../../../assets/icons/location.svg');
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: contain;
    content: '';
    display: block;
    height: rem(24);
    left: 0;
    position: absolute;
    top: 0;
    width: rem(24);
  }
}

.location-address {
  color: var(--accent-interactive);
  font-size: rem(16);
  font-weight: 600;
  line-height: 1.5;

  a {
    display: inline-block;
    text-decoration: none;
  }
}

.location-distance {
  color: var(--gray-3);
  font-size: rem(14);
  font-weight: 600;
  line-height: 1.43;
}

.panel {
  padding: var(--accordion-padding);
  position: relative;

  &::before {
    background-color: var(--separator-color);
    content: '';
    display: block;
    height: 1px;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
  }
}

.panel-content {
  position: relative;
}

.panel-footer {
  align-items: center;
  align-items: flex-end;
  display: flex;
  justify-content: space-between;
  margin-top: rem(16);
}

.panel-footer-left {
  max-width: 300px;
  width: 100%;
}

.panel-footer-right {
  position: relative;
}

.detail {
  position: relative;
}

.detail-title {
  color: var(--custom-gray-5);
  font-size: rem(16);
  font-weight: 700;
  line-height: 1.25;
  margin-bottom: rem(16);
  margin-top: 0;
}

.detail-label,
#reminder-label {
  color: var(--gray-3);
  font-size: rem(14);
  font-weight: 600;
  line-height: 1.43;
}

.detail-info {
  color: var(--custom-gray-5);
  font-size: rem(16);
  font-weight: 600;
  line-height: 1.5;
}

.detail-dropdown {
  .detail-label {
    margin-bottom: rem(2);
  }

  label {
    display: block;
  }
}

.actions {
  align-items: center;
  display: flex;

  button {
    margin-left: rem(16);
  }
}

.alert {
  display: none;
}

@media (max-width: $max-tablet-size) {
  .accordion {
    dt {
      [data-accordion-button-element] {
        bottom: rem(24);
        top: auto;
        transform: none;
      }
    }
  }

  .header-content {
    .col-first {
      margin-bottom: rem(16);
      width: 100%;
    }

    .col-second {
      width: 34%;
    }
  }

  .time {
    border-bottom: 1px solid var(--separator-color);
    padding-bottom: rem(16);
  }

  .panel {
    &::before {
      left: var(--accordion-padding);
      right: var(--accordion-padding);
    }
  }

  .panel-content {
    .col-first {
      width: 33%;
    }

    .col-second {
      width: 35%;
    }

    .col-third {
      padding-right: 0;
    }
  }

  .panel-footer-left {
    max-width: 290px;
  }
}

@media (max-width: $max-phone-size) {
  .accordion {
    dt {
      [data-accordion-button-element] {
        bottom: rem(12);
      }
    }
  }

  .header-content {
    .col-second {
      margin-bottom: rem(16);
      width: 100%;
    }

    .col-third {
      padding-right: 0;
      width: 100%;
    }
  }

  .header-title-mobile {
    display: block;
  }

  .panel-title {
    display: none;
  }

  .detail-title {
    border-top: 1px solid var(--separator-color);
    margin-bottom: 0;
    margin-top: rem(16);
    padding-bottom: rem(2);
    padding-right: rem(90);
    padding-top: rem(18);
  }

  .panel-content {
    .col-first,
    .col-second,
    .col-third {
      margin-bottom: rem(16);
      width: 100%;
    }
  }

  .panel-footer {
    display: block;
    margin-top: 0;
  }

  .panel-footer-left {
    max-width: 100%;
  }

  .actions {
    border-top: 1px solid var(--separator-color);
    display: block;
    margin-top: rem(16);
    padding-top: rem(16);

    button {
      display: block;
      margin-left: 0;
      width: 100%;

      &:not(:last-child) {
        margin-bottom: rem(16);
      }
    }
  }
}
